<template>
    <div>
        <h1 class="title">{{title}} Actor</h1>

        <div class="field">
            <label class="label">First Name</label>
            <div class="control">
                <input class="input" type="text" v-model="actor.firstName" placeholder="First Name" />
            </div>
        </div>

        <div class="field">
            <label class="label">Last Name</label>
            <div class="control">
                <input class="input" type="text" v-model="actor.lastName" placeholder="Last Name" />
            </div>
        </div>

        <div class="field">
            <label class="label">Date of Birth</label>
            <div class="control">
                <input class="input" type="text"  v-mask="'####-##-##'" v-model="actor.dateOfBirth" placeholder="YYYY-MM-DD" />
            </div>
        </div>

        <div class="field is-grouped">
            <p class="control">
                <button v-on:click="cancel" class="button">Cancel</button>
            </p>
            <p class="control">
                <button v-on:click="save" class="button is-primary">Save</button>
            </p>
        </div>

    </div>
</template>

<script>
import {mask} from 'vue-the-mask'
export default {
    name: 'AddActor',
    directives: {mask},
    data: () => ({
        actor: {
            firstName: "",
            lastName: "",
            dateOfBirth: ""
        },
        title: "Add"
    }),
    methods: {
        cancel() {
            this.$router.push({path: '/actors'});
        },
        async save() {
            // let method = this.title === "Add" ? this.$http.post
            const response = this.title === "Add" 
                ? await this.$http.post('http://localhost:8080/api/actors/', this.actor)
                : await this.$http.put(`http://localhost:8080/api/actors/${this.actor.id}`, this.actor)
            console.log(response);
            if (response.status === 200) {
                this.$router.push({path: '/actors'});
            }
        }
    },
    async mounted() {
      console.log(this.$route.params.id)
      if (this.$route.params.id) {
        this.title = "Update"
        const { data } = await this.$http.get(`http://localhost:8080/api/actors/${this.$route.params.id}`)
        this.actor = data
      }

  }
}
</script>